ACA;AACA;EACA,aAAA;EACA,eAAA;CACA;AACA;EACA,YAAA;EACA,sBAAA;EACA,mBAAA;EACA,iBAAA;EACA,wBAAA;EACA,iBAAA;EACA,YAAA;EACA,kBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,YAAA;CACA;AACA,SAAA;AACA;EACA,oBAAA;CACA;AACA;EACA,sBAAA;EACA,iBAAA;EACA,gBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA,UAAA;AACA;EACA,iBAAA;EACA,qBAAA;CACA;AACA;IACA,iBAAA;CACA","file":"Shipped.vue","sourcesContent":["<template lang=\"html\">\n  <div class=\"\">\n    <div class=\"search\">\n     <div>\n           <el-input placeholder=\"请输入服务经理姓名\"  class=\"input-with-select\" v-model='name'>\n              <el-button slot=\"append\" @click='search'>搜索</el-button>\n          </el-input>\n        </div>\n        <div>\n            <el-date-picker\n            v-model=\"picker\"\n            type=\"daterange\"\n            range-separator=\"至 \"\n            start-placeholder=\"开始日期\"\n            @change='search'\n            value-format='yyyy-MM-dd'\n            end-placeholder=\"结束日期\">\n            </el-date-picker>\n        </div>\n    </div>\n    <div class=\"\">\n      <table class=\"table\">\n        <tr>\n          <th>订单编号</th>\n          <th>服务经理姓名</th>\n          <th>收货人姓名</th>\n          <th>收货人电话</th>\n          <th>用户支付时间</th>\n          <th>发货时间</th>\n          <th>收获地址</th>    \n        </tr>\n        <tr v-for=\"item in dataList\">\n          <td class=\"name\">{{item.order_number}}</td>\n          <td>{{item.name}}</td>\n          <td>\n               {{item.man}}\n          </td>\n          <td>{{item.phone}}</td>\n          <td>\n             {{item.time}}\n          </td>\n           <td>\n               {{item.hair_time}}\n           </td>\n           <td >\n            <a href='javascript:;' class='col-1EA' @click='regionDialogVisible=true,reasonData=item'>查看</a>\n          </td>\n          \n        </tr>\n      </table>\n     \n\n      <el-dialog title=\"地区\" :visible.sync=\"regionDialogVisible\" width=\"400px\" center >\n              <div class='reason'>\n                 {{`${reasonData.address}${reasonData.details}`}}\n            </div>\n      </el-dialog>\n\n    \n      <!--分页组件-->\n      <div class=\"page_center\">\n        <paging :page-count=\"pageCount\"   :page=\"page\" @index=\"paging\"></paging>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  export default {\n    data(){\n      return{\n        token:window.sessionStorage.getItem(\"bbytoken\"),\n        pageCount:1,  //总页数,默认1\n        page:1,       //当前页\n        dataList:[],\n        regionDialogVisible:false,\n        reasonData:[],\n        picker:''\n      }\n    },\n    computed:{\n        pickerTime(){\n            let pickerData=this.picker;\n            return{\n                start_time: pickerData && pickerData[0] ? pickerData[0] :'',\n                end_time: pickerData && pickerData[1] ? pickerData[1] :'',\n            }\n        }\n    },\n    methods:{\n      //初始化数据\n      init(){\n      \n        this.$axios.post('admin/SmocRationMsg/totalShipment',{\n            token:this.token,\n            status:1,\n            page:this.page,\n            search:this.name,\n            start_time:this.pickerTime.start_time,\n            end_time:this.pickerTime.end_time\n        }).then(res=>{\n            if(res.data.code==1){\n                this.dataList=res.data.data.list; //拿返回来的数据\n                \n                this.pageCount=res.data.data.rows  //拿取总页数\n            }else{\n                this.dataList={}\n                layer.msg(res.data.msg,{time:1000}) //如果code不等于一代表,没有数据，提示暂无数据\n            }\n        }).catch(err=>console.log(err))\n      },\n      region(id){\n           this.$axios.post('admin/Smoc/areaNum',{\n               token:this.token,\n               id:id,\n               type:1\n           }).then(res=>{\n               if(res.data.code==1){\n                   this.reasonData=res.data.data\n               }\n           }).catch(err=>console.log(err))\n      },\n      /**发货 */\n      DeliverGoods(data){ \n         if(data.status==0){\n             this.$confirm('你确定要收货吗, 是否继续?', '提示', {\n                        confirmButtonText: '确定',\n                        cancelButtonText: '取消',\n                        type: 'warning'\n                    }).then(res=>{\n                         this.$axios.post('admin/SmocRation/deliverGoods',{\n                            token:this.token,\n                            sm_id:data.sm_id,\n                            ration:data.number,\n                            id:data.id,\n                            sm_area_id:data.sm_area_id\n                        }).then(res=>{\n                            if(res.data.code==1){\n                                this.$message({message:res.data.msg,type:'success'})\n                                this.init()\n                            }\n                        }).catch(err=>{\n                            this.$message.error(res.data.msg)\n                        })\n                    }).catch(err=>{})\n         }\n      },\n      //搜索\n      search(){\n        this.init()\n      },\n      //点击查看投诉人详情\n      ComplaintDialog(id){\n        this.ComplaintVisible=true\n        this.$axios.post('Smoc/tsDetail',{token:this.token,id:id}).then(res=>{\n          if(res.data.code==1){\n          \n          this.Complaintlist=res.data.data\n        }\n      }).catch(err=>console.log(err))\n      },\n      //投诉内容弹框\n      ComplaintContent(id){\n        this.ComplaintContentVisible=true\n        this.$axios.post('Smoc/xcl',{token:this.token,id:id}).then(res=>{\n          if(res.data.code==1){\n         \n          this.Complaintdata=res.data.data\n        }\n      }).catch(err=>console.log(err))\n      },\n      //操作分页组件\n      paging(e){\n        //把当前页赋值给page\n        this.page=e;\n        // 当前页发生改变时调取注册审核列表接口,重新渲染\n        this.init()\n      }\n    },\n    mounted(){\n      this.init()\n      this.$store.commit('increment')\n    }\n  }\n</script>\n\n<style lang=\"css\" scoped=\"scoped\">\n  .Head_portrait{\n    width: 50px;\n    height: 50px;\n    vertical-align: middle;\n    margin: 5px 0;\n    border-radius: 50%;\n  }\n  .search{\n    margin: 15px 0 15px 30px;\n    display: flex;\n    align-items: center;\n  }\n  .search>div{\n     margin-right: 20px;\n  }\n   .search >>> .el-range-separator{\n     padding: 0\n  }\n  .search .input{\n    width: 25.9%;\n  }\n  .Conduct>a{\n    margin-left: 20px;\n  }\n  .name{\n    width: 10%;\n  }\n  .table th{\n    height: 50px !important;\n    line-height: 50px !important;\n  }\n  .content{\n    width: 150px;\n    margin: 0 auto;\n  }\n  .Reason{\n    width: 50px;\n    display: inline-block;\n    white-space:nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    text-align: left;\n    float: left;\n    margin-left: 40px;\n  }\n  .rel{\n    position:relative;\n  }\n  .See{\n    float: left;\n  }\n  /*投诉人弹框*/\n  .ComplaintTable{\n    margin-bottom: 20px;\n  }\n  .ComplaintTable>tr>td{\n    width: 50% !important;\n    text-align: left;\n    padding: 0 10px;\n  }\n  .ComplaintTable>tr>td:last-child{\n    text-align: right;\n  }\n  /*投诉内容弹框*/\n  .ComplaintContent{\n    margin-top: 20px;\n    padding-bottom: 40px;\n  }\n  .reason{\n      min-height: 100px\n  }\n</style>\n<style >\n  .el-select .el-input {\n    width: 130px;\n    margin-right: 20px;\n  }\n\n  .search_btn{\n    border-radius: 0;\n    height: 38px;\n    width: 120px;\n  }\n  .input-with-select .el-input-group__prepend {\n    background-color: #fff;\n  }\n  .el-dialog__header{\n    padding-bottom: 20px;\n    border-bottom: 1px solid #E6E6EB;\n  }\n  .el-dialog__body{\n    padding-top: 0 !important;\n  }\n</style>\n"],"sourceRoot":""}]);

